.sidebar {
  overflow: hidden ;
  border-right: 0.5px solid rgb(223, 219, 219);
  height: 100vh;
  box-shadow: 0px 0px 0.5px ;
  background-color: #001253;
  z-index: 1300;
  width: 55px;

  //margin-top: 64px;
  & > .header {
    margin:10px 7px 10px 7px;
    margin-bottom: -10px;
    text-align: center;

  }
  & > .content {
    display: flex;
    flex-direction: column;
    height: calc(100% - 64px);
    justify-content: space-between;
  }
  & > .content > .container {
    width: 100%;
    overflow: hidden ;
    height: 100%;
    margin-top: 30px;
  }
  & > .content > .footer {
    overflow: hidden ;
    width: 100%;
    padding: 3px;
  }
}